@font-face {
  font-family: 'Unica One';
  src: url(./UnicaOne-Regular.ttf);
}
body {
  margin: 0;
  font-family: 'Unica One', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  text-align: center;
}
#main {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
#clock {
  background-color: white;
}
.trigger {
  width: 20vw;
  height: 20vh;
  position: fixed;
  z-index: 9;
  cursor: pointer;
}
.trigger-top-left {
  top: 0;
  left: 0;
}
.trigger-top-right {
  top: 0;
  right: 0;
}
.trigger-bottom-left {
  bottom: 0;
  left: 0;
}
.trigger-bottom-right {
  bottom: 0;
  right: 0;
}